import Banner from "../components/Banner"

function Politica() {
    return (
        <div className="bg-jj"><br></br><br></br><br></br>
            <h1 className="text-center">Política de privacidade</h1><br></br>
                <div className="bg-jj mx-auto max-w-2xl">
                    <p>
                    O site NeoDrop é de propriedade da empresa NeoDrop, que é a controladora de seus dados pessoais. Nós adotamos esta Política de Privacidade, que determina como nós estamos processando as informações coletadas pelo site NeoDrop e também explica por quais razões nós precisamos coletar dados pessoais sobre você. Portanto, você deve ler esta Política de Privacidade antes de usar o site NeoDrop.<br></br>
                    </p>
                    <p>
                    Nós cuidamos dos seus dados pessoais e assumimos a responsabilidade de garantir a confidencialidade e segurança de suas informações pessoais. 
                    </p>
                    <p>
                    Estes são as informações pessoais que coletamos: Quando você visita o site NeoDrop, nós automaticamente coletamos certas informações, incluindo informações sobre seu navegador, endereço IP, fuso horário e alguns dos cookies instalados no seu dispositivo. Além disso, quando você navega pelo Site, nós coletamos informações sobre as páginas individuais ou produtos que você visualiza, sobre quais sites ou termos de busca redirecionaram você para nosso Site, e sobre como você interage com o Site. Nós nos referimos a essas informações coletadas automaticamente como "Informações sobre o dispositivo". Além disso, nós podemos coletar dados pessoais que você fornecer (incluindo, mas não limitado a: Nome, Sobrenome, Endereço, informações de pagamento, etc) durante o processo de registro para poder cumprir o acordo.<br></br>
                    </p>
                    <p>
                    Por que fazemos o processamento dos seus dados? Nossa maior prioridade é a segurança dos dados pessoais dos usuários e, portanto, nós podemos processar apenas dados mínimos, apenas enquanto for absolutamente necessário para a manutenção do site. Informações coletadas automaticamente são usadas para identificar possíveis casos de abuso e estabelecer dados estatísticos sobre o uso do site.<br></br>
                    </p>
                    <p>
                    Você pode visitar o site sem nos contar sobre quem você é ou relevar qualquer informação que possa ser usada por outra pessoa para identificar você individualmente. Se, apesar disso, você quiser utilizar algum dos recursos do site, ou quiser receber nossa newsletter, ou quiser conceder outros detalhes através do preencimento e envio de formulários, você poderá fornecer dados pessoais para nós, como seu email, nome, sobrenome, cidade de residência, organização e número de telefone. Você pode escolher não fornecer dados pessoais para nós, mas, dessa forma, talvez você não consiga usar alguns dos resursos do site. Por exemplo, você não vai conseguir receber nossa Newsletter ou entrar em contato conosco diretamente pelo nosso site. Usuários que não tenham certeza sobre quais informações pessoais são obrigatórias são convidados a entrarem em contato conosco pelo e-mail NeoDrop@gmail.com.<br></br>
                    </p>
                </div>
            <Banner/>
        </div>
    )
}

export default Politica